import { Component, OnInit, ViewChild } from '@angular/core';
import { DataTableDirective } from 'angular-datatables';
import { RequirementService } from '../../_services/requirement.service';
import { CommitShortfallDetailList } from '../../_models/CommitShortfallDetailList';
import { Subject } from 'rxjs';
import { AlertifyService } from '../../_services/alertify.service';
import { CPOList } from '../../_models/CPOList';
import { CommitOrderData } from '../../_models/CommitOrderData';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { COLDetailsList } from '../../_models/COLDetailsList';
import { CPLDetailsList } from '../../_models/CPLDetailsList';

@Component({
  selector: 'app-commit-shortfall',
  templateUrl: './commit-shortfall.component.html',
  styleUrls: ['./commit-shortfall.component.css']
})
export class CommitShortfallComponent implements OnInit {
  items: CommitShortfallDetailList[];
  currentmonth: any = {};
  nextmonth: any = {};
  postnextmonth: any = {};
  mymodel: any = {};
  type1: any = {};
  type2: any = {};
  currentcomodel:  any = {};
  currentcpomodel:  any = {};
  currentcoproc:  any = {};
  currentcpoproc:  any = {};
  selectedconum:  any = {};
  selectedponum:  any = {};
  poorders: CPOList[];
  orders: CommitOrderData[];
  coldetails: COLDetailsList[];
  cplorderdetails: CPLDetailsList[];
  @ViewChild(DataTableDirective) dtElement: DataTableDirective;
  @ViewChild('CPODetails') public CPODetails: ModalDirective;
  @ViewChild('CODetails') public CODetails: ModalDirective;
  @ViewChild('COLDetails') public COLDetails: ModalDirective;
  @ViewChild('CPOLDetails') public CPOLDetails: ModalDirective;
  dtOptions: DataTables.Settings = {
    pagingType: 'full_numbers',
    pageLength: 25,
    retrieve: true,
    order: [[0, 'desc']],
    destroy: true
  };
  dtInstance: DataTables.Api;
  isDtInitialized: boolean = false;
  dtTrigger: Subject<CommitShortfallDetailList> = new Subject();
  dtTrigger1: Subject<CPOList> = new Subject();
  dtTrigger2: Subject<CommitOrderData> = new Subject();

  constructor(private requirementservice: RequirementService, private alertify: AlertifyService) { }

  ngOnInit(): void {
    const date = new Date();  // 2009-11-10
    const date1 = new Date(date.getFullYear(), date.getMonth() + 2, 0);
    const date2 = new Date(date.getFullYear(), date.getMonth() + 3, 0);
    this.currentmonth = date.toLocaleString('en-GB', { month: 'long' });
    this.nextmonth = date1.toLocaleString('en-GB', { month: 'long' });
    this.postnextmonth = date2.toLocaleString('en-GB', { month: 'long' });
    this.mymodel.inorder = 0;
    this.mymodel.inpo = 0;
    this.loadOrders();
  }
  loadOrders() {
    return this.requirementservice.getCommitShortFalldetails(0, 0).subscribe((items: CommitShortfallDetailList[]) => {
      this.items = items;
      if (this.isDtInitialized) {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
          dtInstance.destroy();
          this.dtTrigger.next();
        });
      } else {
        this.isDtInitialized = true;
        this.dtTrigger.next();
      }
    }, error => {
      this.alertify.error(error);
    });

  }

  activateModel(event, flag: string) {
    this.mymodel.status = 0;
    if (event.target.checked) {
      this.mymodel.status = 1;
    }
    if (flag === 'InOrder') {
      this.mymodel.inorder = this.mymodel.status;
    } else if (flag === 'InPurchase') {
      this.mymodel.inpo = this.mymodel.status;
    }
    this.type1 = this.mymodel.inorder;
    this.type2 = this.mymodel.inpo;
    return this.requirementservice.getCommitShortFalldetails(this.type1, this.type2)
    .subscribe((items: CommitShortfallDetailList[]) => {
      this.items = items;
      if (this.isDtInitialized) {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
          dtInstance.destroy();
          this.dtTrigger.next();
        });
      } else {
        this.isDtInitialized = true;
        this.dtTrigger.next();
      }
    }, error => {
      this.alertify.error(error);
    });
  }
  CheckCPODetails(modelname: string, proc_name: string) {
    this.currentcpomodel = modelname;
    this.currentcpoproc = proc_name;
    return this.requirementservice.getCommitPurchaseOrders().subscribe((poorders: CPOList[]) => {
      this.poorders = poorders.filter(proj => (proj.model_name === modelname) && (proj.proc_name === proc_name));
      if (this.isDtInitialized) {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
          dtInstance.destroy();
          this.dtTrigger1.next();
        });
      } else {
        this.isDtInitialized = true;
        this.dtTrigger1.next();
      }
      this.CPODetails.show();
    }, error => {
      this.alertify.error(error);
    });
  }
  CheckCOPLDetails(id: number, co_num: string) {
    this.selectedponum = co_num;
    return this.requirementservice.getCPODetails(id).subscribe((cplorderdetails: CPLDetailsList[]) => {
      this.cplorderdetails = cplorderdetails;
      this.CPOLDetails.show();
    }, error => {
      this.alertify.error(error);
    });
  }
  CheckCODetails(modelname: string, proc_name: string) {
    this.currentcomodel = modelname;
    this.currentcoproc = proc_name;
    return this.requirementservice.getCommitOrders().subscribe((orders: CommitOrderData[]) => {
      this.orders = orders.filter(proj => (proj.modelName === modelname) && (proj.processor === proc_name));
      if (this.isDtInitialized) {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
          dtInstance.destroy();
          this.dtTrigger2.next();
        });
      } else {
        this.isDtInitialized = true;
        this.dtTrigger2.next();
      }
      this.CODetails.show();
    }, error => {
      this.alertify.error(error);
    });
  }
  CheckDetails (id: number, co_num: string) {
    this.selectedconum = co_num;
    return this.requirementservice.getCODetails(id).subscribe((coldetails: COLDetailsList[]) => {
      this.coldetails = coldetails;
      this.COLDetails.show();
    }, error => {
      this.alertify.error(error);
    });
  }
}
